import { Injectable } from '@angular/core';
import { Router, RoutesRecognized } from '@angular/router';
import { BehaviorSubject, Subject } from 'rxjs';
import { filter, pairwise } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AppService {

  currentState: Subject<any> = new Subject();
  currentValue = new BehaviorSubject<any>({});
  getValue = this.currentValue.asObservable();

  private excludedUrl = ['/login', '/registration/', '/forgot-password', 'change-password'];
  private previousUrl: string;

  constructor(private router: Router) {
    this.router.events
    .pipe(filter((evt: any) => evt instanceof RoutesRecognized), pairwise())
    .subscribe((events: RoutesRecognized[]) => {
      this.previousUrl = events[0].urlAfterRedirects;
    });
  }

  addValue(key: string, value: string) {
    this.currentState.next({ key, value });
  }

  setValue(key: string, value: string) {
    this.currentValue.next({ key, value });
  }

  canNavigateBack() {
    return this.previousUrl && !this.excludedUrl.some((url) => this.previousUrl.startsWith(url));
  }

}
