import { Component, Input } from '@angular/core';
import { RBACINFO } from './pics-dynamic/@core/urls/rbac-url.config';
import { Observable } from 'rxjs';
import { PermissionStore } from './pics-dynamic/@core/permissions/permission.store';
import { PlatformDataStoreService } from './pics-dynamic/@core/service/platform-data-store.service';

@Component({
  selector: 'dynamicdata',
  template: `
    <router-outlet></router-outlet>
  `,
  styles: [
  ]
})
export class DynamicComponent {
  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public COMMONSERVICE!: Observable<any>;

  constructor(
    private permissionStore: PermissionStore,
    private _storeservice: PlatformDataStoreService
  ) {
  }
  ngOnInit() {
     this.COMMONSERVICE.subscribe(val => {
      if(val){
        this.RBACORG = val.RBACORG;
        this.PERMISSION = val.PERMISSION;
        this._storeservice.setData('RBACORG', this.RBACORG);
        this.permissionStore.setStore(this.PERMISSION);
        this._storeservice.setData('PERMISSION', val.PERMISSION);
        this._storeservice.setData('HTTPSERVICE',val.HTTPSERVICE);
        this._storeservice.setData('ALERTSERVICE',val.ALERTSERVICE);
        this._storeservice.setData('APPSERVICE',val.APPSERVICE);
        this._storeservice.setData('SHAREDSERVICE',val.SHAREDSERVICE);
      }
     })
  }
}
