import { Injectable } from '@angular/core';
import { OcrValidationServiceConfig } from '../urls/ocr-validation-url.config';
import { PlatformDataStoreService } from './platform-data-store.service';

@Injectable({
  providedIn: 'root'
})
export class OcrValidationService {
  httpService:any;
  constructor(private _storeservice:PlatformDataStoreService) {
    console.log('log');
    this._storeservice.currentStore.subscribe((res: any) => {
      if(res){
        this.httpService = res['HTTPSERVICE']
      }
    })
  }

  getUpload(obj) {
    return this.httpService.post(OcrValidationServiceConfig.EndPoint.OCRValidate.GetNewOcrData, JSON.parse(obj));
  }
}
