import { createSelector } from '@ngrx/store';
import { ComponentState, MasterViewState } from './master-view.state';
import { selectGridsterState } from '../../@core/core.state';

// Selector to get all gridster items (components) from the state
export const selectAllGridsterItems = createSelector(
  selectGridsterState,
  (state: MasterViewState) => state.comps
);

// Selector to get the event associated with the gridster components
export const selectEvent = createSelector(
  selectGridsterState,
  (state: MasterViewState) => state.compsevent
);

// Selector to get a specific component configuration by ID
export const selectComponentConfigById = (componentId: string) => createSelector(
  selectAllGridsterItems,
  (components: ComponentState[]) => components.find(comp => comp.id === componentId)
);
